.header{
    display: flex;
    justify-content: space-between;
    background-color: #649acc;
    align-items: center;
    padding: 10px 15px;
    width: 100%;
}
.header_logo a{
    text-decoration: none;
    font: 700 20px Montserrat;
    color: #fff;
}
.header_logo a span {
    text-transform: uppercase;
}
.hidden{
    display: none;
}
.header_cart{
    width: 35px;
}
.card_link{
    position: relative;
}
.card_link::after{
    content: attr(data-count);
    position: absolute;
    background-color: red;
    padding: 2px 5px;
    right: 25px;
    top: -5px;
    border-radius: 15px;
    font-size: 10px;
    color: #fff;
}
.card_link[data-count="0"]::after{
    display:none;
}
.signout_btn{
    padding: 7px 10px;
    font: 600 14px Open Sans;
    cursor: pointer;
    color: #274f7d;
    background-color: #fff;
    border: none;
    border-radius: 5px;
    transition: .5s all ease;
}
.signout_btn:hover{
    background-color: #233A58;
    color:#a9cdf1;
}
.header_avatar{
    width: 35px;
    height: 35px;
    border: 2px solid #fff;
    border-radius: 50%;
    background: #fff;
    animation: avatarRotate 3s infinite;
}
.header_management:last-child{
    font: 700 16px Montserrat;
    color:#fff;
}

.header_management_desktop{
    display: flex;
    gap: 15px;
    align-items: center;
}
.menu_btn{
    display: none;
    width: 30px;
    height: 30px;
    background: #fff;
    padding: 2px;
    border: none;
    border-radius: 5px;
}
.menu_btn::after{
    content: attr(data-count);
    position: absolute;
    background-color: red;
    padding: 2px 5px;
    right: 10px;
    top: 7px;
    border-radius: 15px;
    font-size: 10px;
    color: #fff;
}
.menu_btn[data-count="0"]::after{
    display:none;
}
.menu_btn img{
    width: 25px;
    height: 25px;
}

.header_management_mobile_overlay,
.header_management_mobile{
    padding: 10px;
    display: flex;
    position: fixed;
    top: 0;
    right: -40px;
    width: 0%;
    height: 100vh;
    transition: .3s all ease-in-out;
}
.header_management_mobile_overlay{
    background: #649acc9d;
    z-index: 2;
}
.header_management_mobile{
    flex-flow: column;
    max-width: 40%;
    background: #649acc;
    z-index: 3;
    border-left: 3px solid #fff;
}
.visible_menu,
.visible_overlay{
    width:100%;
    right: 0px;
    display: flex;
}

.menu_avatar{
    border-radius: 50%;
    border: 3px solid #FFF;
    background: #fff;
    max-width: 150px;
    width: 100%;
    margin: 0 auto;
}
.menu_user{
    font: 700 20px Montserrat;
    text-transform: uppercase;
    margin: 5px auto;
}

.menu_card_link,
.menu_signout_btn,
.menu_books_link{
    margin: 5px 0;
    cursor: pointer;
    border: none;
    font: 700 14px Montserrat;
    text-decoration: none;
    color: #649ACC;
    text-align: center;
    padding: 5px;
    background: #fff;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.menu_cart_count{
    color: #fff;
    margin-left: 5px;
    background: red;
    padding: 2px 8px;
    border-radius: 10px;
}

@keyframes avatarRotate { 
    0%{
        transform: rotate(0deg);
    }
    50%{
        transform: rotate(10deg);
    }
    100%{
        transform: rotate(0deg);
    }
}

@media (min-width: 320px) and (max-width: 767px) {
    .header_management_desktop{
        display: none;
    }
    .menu_btn{
        display: block;
    }
}
